


























import {Component, Prop, Vue} from "vue-property-decorator"
import Portal from "@/managers/portal"
import {IStudentProfile} from "@chaosinsight/yogipeople-portalclient"
import {IPaymentMethod} from "@chaosinsight/yogipeople-portalclient/dist/extensions/payments"
import CardAdd from "@/components/CardAdd.vue"
import RedeemVoucher from "@/components/RedeemVoucher.vue"
import MessageBar from "@/components/MessageBar.vue"

@Component({ components: { CardAdd, MessageBar, RedeemVoucher } })
export default class UserPayment extends Vue {
	@Prop()
	private profile!: IStudentProfile

	private paymentMethods: IPaymentMethod | null = null
	private addCard: boolean | null = null

	private voucher: string = ""

	private toRemove: string | null = null
	private temp: string | null = ""
	private created(): void {
		this.getPaymentMethods()
		this.temp = window.localStorage.getItem("paymentroute")
		window.localStorage.removeItem("paymentroute")
	}
	private mounted(): void {
		if (window.localStorage.getItem("Login") === "Logout")
			this.$router.push("/signin")
		}
	private close(): void {
		this.$emit("close")
		if (this.temp === "zero")
			this.$router.push("/home")
			}
	/**
	 * Get existing payment methods
	 */
	private getPaymentMethods(): void {
		Portal.client.call.payments.paymentMethodsGet("card")
			.response
			.then(res => {
				this.paymentMethods = res
				this.addCard = null
				this.toRemove = null
			})
			}
	private addingCard(): void {
		if (this.paymentMethods !== null)
			this.addCard = false
		else this.addCard = true
		}
	/**
	 * Delete payment method
	 */

	private deletePaymentMethod(id: string): void {
		this.toRemove = id
		Portal.client.call.payments.paymentMethodsDelete(id)
			.response
			.then(() => setTimeout(() => this.getPaymentMethods(), 800))  // better ux: loader min 800ms
	}
}
